export default class bullet {
  constructor(xLoc, yLoc, game, bulletSpd) {
    this.bulletSpd = bulletSpd;
    this.xLoc = xLoc;
    this.yLoc = yLoc;
    this.game = game;
  }

  update() {
    this.xLoc += this.bulletSpd;

    /* to save memory, we'll delete any bullets that leave the screen*/
    if (this.xLoc > this.game.gameWidth) {
      this.game.removeObject(this);
    }
  }

  draw(ctx) {
    ctx.beginPath();
    ctx.arc(this.xLoc, this.yLoc, 10, 0, 2 * Math.PI);
    ctx.stroke();
    ctx.fillStyle = "#00";
    ctx.fill();
  }
}
