import "./styles.css";
import Player from "./player.js";
import keyInput from "./inputHandler";
import playerBullet from "./bullet.js";

export default class Game {
  constructor(gameWidth, gameHeight) {
    this.gameWidth = gameWidth;
    this.gameHeight = gameHeight;
  }

  start() {
    this.player = new Player(this);
    this.gameObjects = [this.player];
    new keyInput(this.player);
  }

  addObject(object) {
    this.gameObjects.push(object);
  }
  removeObject(object) {
    var index = this.gameObjects.indexOf(object);
    if (index !== 1) {
      this.gameObjects.splice(index, 1);
    }
  }

  update(dt) {
    this.gameObjects.forEach((element) => element.update(dt));
  }

  draw(ctx) {
    this.gameObjects.forEach((element) => element.draw(ctx));
  }
}
