import bullet from "./bullet.js";
import keyInput from "./inputHandler.js";

export default class player {
  constructor(game) {
    this.width = 50;
    this.height = 50;
    this.game = game;

    this.maxSpd = 50;
    this.currVel = {
      x: 0,
      y: 0
    };
    this.position = {
      x: 0,
      y: (game.gameHeight - this.height) / 2
    };

    this.maxFireDelay = 5;
    this.currFireDelay = 0;
  }

  moveUp(x) {
    if (x === 1) {
      this.currVel.y = -this.maxSpd;
    } else {
      this.currVel.y = this.maxSpd;
    }
  }

  moveRight(x) {
    if (x === 1) {
      this.currVel.x = -this.maxSpd;
    } else {
      this.currVel.x = this.maxSpd;
    }
  }

  stopUp() {
    this.currVel.y = 0;
  }

  stopRight() {
    this.currVel.x = 0;
  }

  fireBullet() {
    if (this.currFireDelay <= 0) {
      this.game.addObject(
        new bullet(this.position.x, this.position.y, this.game, 10)
      );
      this.currFireDelay = this.maxFireDelay;
    }
  }

  draw(ctx) {
    ctx.fillRect(
      this.position.x,
      this.position.y - this.height / 2,
      this.width,
      this.height
    );
  }

  update(dt) {
    this.currFireDelay--;
    if (!dt) return;
    this.position.y += this.currVel.y / dt;
    this.position.x += this.currVel.x / dt;
  }
}
